.page {

  &__body {
    margin: 0;
    padding: 0;
    font-family: Roboto, sans-serif;
  }
}

h2, p {
  margin: 0;
}

.container {
  --column-count: 1;
  
  display: grid;
  grid-template-columns: repeat(var(--column-count), 200px);
  gap: 46px 48px;
  padding: 50px 40px;
  justify-content: center;
}

@media (min-width: 488px) {
  .container {
    --column-count: 2;
  }
}

@media (min-width: 768px) {
  .container {
    --column-count: 3;
  }
}

@media (min-width: 1024px) {
  .container {
    --column-count: 4;
  }
}
